.fontTest {
    text-align: center;
    font-family: 'NotoSans';
}

.container {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #FFF5DC;
}

.frame {
    position: relative;
    width: 336px;
    height: 336px;
    background-color: white;
}

.color-picker-box {
    display: flex;
    flex-direction: row-reverse;
    position: fixed;
    align-items: start;
    justify-content: end;
    top: 0px;
    height: 46px;
    width: 100%;
    padding-top: 8px;
    background-color: white;
    border-bottom: 1px solid #FFBB6D;
}
.color-picker-container {
    position: fixed;
    bottom: 0px;
    right: 0px;
    height: 100%;
    width: 30vh;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: white;
    border-left: 1px solid #FFBB6D;
}

.color-picker-header {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
}

.color-picker-header span {
    margin-right: auto;
}
.color-picker {
    margin: 10px;
}

.color-picker-icon {
    height: 38px;
    width: 38px;
    margin: 0px 8px 0px 8px;
}

.left-menubar {
    position: fixed;
    bottom: 0px;
    left: 0px;
    height: 100%;
    width: 86px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: white;
    border-right: 1px solid #FFBB6D;
}

.menu-icon {
    padding-top: 3px;
}

.left-drawer {
    display: flex;
    flex-direction: column;
    position: fixed;
    bottom: 0px;
    left: 86px;
    height: 95.5vh;
    width: 270px;
    background-color: white;
    border-right: 1px solid #FFBB6D;
    padding-top: 38px;
}

.drawer-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
}

.drawer-basic-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    border: 1px solid #d5d4d4;
    border-radius: 5px;
    cursor: pointer;
    background-color: #fff;

}

.basic-icon-grid {
    display: grid;
    grid-template-columns: repeat(3, min-content);
    gap: 3px;
    padding: 0; /* padding을 제거하여 간격을 최소화 */
}

.basic-icon-grid-container {
    display: flex;
    margin-left: 40px;
    align-items: center;
    width: 100%;
}

.drawer-icon-button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 99%;
    height: 40px;
    margin :2px;
    background-color: '#E6E0DC';
    border: none; /* 테두리 경계선 없애기 */
    border-radius: 5px; /* 모서리를 둥글게 */
    cursor: pointer; /* 마우스 커서를 포인터로 변경 */
    padding: 10px 20px; /* 패딩 추가 */
}

.subtitle{
    margin-top: -3px;

}